import React from 'react';

class Signout extends React.Component{
    constructor(props){
        super(props);
    }
    submitCallback(data){
        window.location.reload();
    }
    render(){
        return (
            <form method="POST" action='/user/signout' id='signout' onSubmit={this.props.submitForm.bind(this, 'signout')}>
                <div className='ui-dialog-head'></div>
                <div className='ui-dialog-body'>
                    <p></p>
                </div>
                <div className='ui-dialog-foot'>
                    <button type='submit' className='ui-btn ui-btn-error'></button>
                    <button type='button' className='ui-btn ui-btn-default' onClick={this.props.hideModal.bind('none')}></button>
                </div>
            </form>
        )
    }
}

export default Signout;


