import React from 'react';
import InputGroup from 'components/InputGroup/InputGroup';

class MovieForm extends React.Component {
    constructor (props){
        super(props);
    }
    render() {
        const { items } = this.props;
        return (
            <div className="ui-form-body">
                <InputGroup type='text' title='' name='movie[title]' val={items.title} placeholder=''/>
                <InputGroup type='text' title='' name='movie[doctor]' val={items.doctor} placeholder=''/>
                <InputGroup type='text' title='' name='movie[country]' val={items.country} placeholder=''/>
                <InputGroup type='text' title='' name='movie[language]' val={items.language} placeholder=''/>
                <InputGroup type='text' title='' name='movie[year]' val={items.year} placeholder=''/>
                <InputGroup type='text' title='' name='movie[summary]' val={items.summary} placeholder=''/>
                <InputGroup type='text' title='' name='movie[poster]' val={items.poster} placeholder=''/>
                <InputGroup type='text' title='' name='movie[flash]' val={items.flash} placeholder=''/>
            </div>
        );
    }
}
MovieForm.defaultProps = {
    items : {
        title : '',
        doctor : '',
        country : '',
        language : '',
        year : '',
        summary : '',
        poster : '',
        flash : ''
    }
};
MovieForm.PropsType = {
    items : React.PropTypes.object
}

export default MovieForm;



